.navigator {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #cfc3bc;
    padding-bottom: 5px;
}

li {
    display: inline;
    align-self: center;
    margin: 0;
    margin-left: 15px;
}

ul {
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
}

a {
    color: #cfc3bc;
    text-decoration: none;
}

.site-name {
    margin: 0;
    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
    color: #cfc3bc;
}

.nav-menu {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 15px;
    color: #cfc3bc;
}