.greeting {
    margin-top: 100px;
    font-family: 'Montserrat', sans-serif;
    color: #cfc3bc;
    font-weight: 500;
}

.korean-title {
    overflow: hidden;
    /* 타이핑 애니메이션 효과 */
    border-right: 0.15em solid #cfc3bc;
    /* 타이핑 커서 */
    white-space: nowrap;
    /* 텍스트가 한 줄에 표시되도록 */
    letter-spacing: 0.15em;
    /* 타이핑 애니메이션 간격 */
    animation: typing 2.5s steps(30, end), blink-caret 0.5s step-end infinite;
}

.korean-text {
    overflow: hidden;
    /* 타이핑 애니메이션 효과 */
    border-right: 0.15em solid #cfc3bc;
    /* 타이핑 커서 */
    white-space: nowrap;
    /* 텍스트가 한 줄에 표시되도록 */
    letter-spacing: 0.15em;
    /* 타이핑 애니메이션 간격 */
    animation: typing 1.8s steps(30, end), blink-caret 0.5s step-end infinite;
}

.english-text {

    overflow: hidden;
    /* 타이핑 애니메이션 효과 */
    border-right: 0.15em solid #cfc3bc;
    /* 타이핑 커서 */
    white-space: nowrap;
    /* 텍스트가 한 줄에 표시되도록 */
    letter-spacing: 0.15em;
    /* 타이핑 애니메이션 간격 */
    animation: typing 1.5s steps(30, end), blink-caret 0.5s step-end infinite;
}

@keyframes typing {
    from {
        width: 0;
    }

    to {
        width: 100%;
    }
}

@keyframes blink-caret {

    from,
    to {
        border-color: transparent;
    }

    50% {
        border-color: #cfc3bc;
    }
}