.my-projects {
    margin-top: 100px;
    text-align: center;
}

.my-projects-title {
    font-family: 'Montserrat', sans-serif;
    font-size: 40px;
    font-weight: 700;
    color: #cfc3bc;
    margin-bottom: 50px;
    text-align: left;
}

.project {
    margin: 20px 0;
    padding: 20px;
    border-radius: 8px;
}

.project h3 {
    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
    color: #cfc3bc;
}

.project img {
    width: 100%;
    height: auto;
    margin-top: 20px;
}

.project p {
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 16px;
    color: #cfc3bc;
    margin-top: 20px;
}

.project a {
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 16px;
    color: #cfc3bc;
    text-decoration: none;
    margin-top: 20px;
    display: block;
    transition: color 0.3s ease;
}

.project a:hover {
    color: #ffffff;
}