.work-footer {
    color: #cfc3bc;
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;
}

.contact {
    font-family: 'Montserrat', sans-serif;
    font-size: 40px;
    font-weight: 650;
    margin-bottom: 40px;
    position: relative;
    /* 가상 요소 위치를 위한 설정 */
}

.contact::after {
    content: '';
    display: block;
    width: 0;
    height: 2px;
    background: #cfc3bc;
    position: absolute;
    /* 부모 요소에 상대적인 위치 */
    bottom: -2px;
    /* 부모 요소의 하단에 위치 */
    left: 0;
    /* 왼쪽부터 시작 */
    animation: border-expand 1.5s ease-out forwards;
}

@keyframes border-expand {
    from {
        width: 0;
    }

    to {
        width: 100%;
    }
}

.links a {
    color: #cfc3bc;
    text-decoration: none;
    margin-right: 20px;
    transition: color 0.3s ease;
}

.links a:hover {
    color: #ffffff;
}

.links {
    display: flex;
    flex-direction: row;
    margin-bottom: 20px;
}

p {
    margin: 0;
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 16px;
}